import { forwardRef, PropsWithoutRef, useRef, FC, useEffect, HTMLAttributes } from 'react';

import {
  useConsolidatedRef,
  useEscape,
  useFocusTrap,
  useI18n,
  useOuterEvent,
  useUID
} from '../../hooks';
import Text from '../Text';
import Icon from '../Icon';
import Progress from '../Progress';
import Flex from '../Flex';
import SummaryItem from '../SummaryItem';

import { StyledChildrenWrap, StyledDialogContent, StyledDialogHeader } from './Dialog.styles';
import { InfoDialogProps } from './Dialog.types';
import { StyledDismissButton, StyledInfoDialog } from './InfoDialog.styles';

const InfoDialog: FC<InfoDialogProps & HTMLAttributes<HTMLDivElement>> = forwardRef(
  (
    {
      target,
      heading: headingProp,
      children,
      progress: progressProp,
      onDismiss,
      ...restProps
    }: PropsWithoutRef<InfoDialogProps>,
    ref: InfoDialogProps['ref']
  ) => {
    const t = useI18n();
    const headingId = useUID();
    const dialogRef = useConsolidatedRef(ref);
    const closeButtonRef = useRef<HTMLButtonElement>(null);

    useEscape(() => {
      onDismiss?.();
    });

    useOuterEvent('mousedown', [target, dialogRef], () => {
      onDismiss?.();
    });

    useFocusTrap(dialogRef);

    useEffect(() => {
      if (!progressProp) {
        closeButtonRef.current?.focus();
      }
    }, [progressProp]);

    useEffect(() => {
      return () => {
        if (document.contains(target)) {
          target.focus();
        }
      };
    }, []);

    const heading = headingProp ? (
      <Text id={headingId} variant='h2'>
        {typeof headingProp === 'string' ? headingProp : headingProp.primary}
      </Text>
    ) : null;

    const progress = (
      <Progress
        visible={!!progressProp}
        focusOnVisible
        placement={children ? 'local' : 'block'}
        message={typeof progressProp === 'string' ? progressProp : undefined}
      />
    );

    const dismissButton = (
      <StyledDismissButton
        icon
        variant='simple'
        onClick={() => {
          onDismiss();
        }}
        label={t('close')}
        ref={closeButtonRef}
      >
        <Icon name='times' />
      </StyledDismissButton>
    );

    return (
      <StyledInfoDialog
        {...restProps}
        {...(typeof headingProp === 'string'
          ? { labelledbyId: headingId }
          : { ariaLabel: headingProp?.primary })}
        arrow
        target={target}
        progress={progressProp}
        ref={dialogRef}
      >
        {headingProp && heading && (
          <Flex
            container={{ alignItems: 'center', justify: 'between', pad: 2, gap: 1 }}
            item={{ shrink: 0 }}
            as={StyledDialogHeader}
          >
            {typeof headingProp === 'string' ? (
              heading
            ) : (
              <SummaryItem
                primary={heading}
                secondary={
                  headingProp.secondary ? (
                    <Text variant='secondary'>{headingProp.secondary}</Text>
                  ) : undefined
                }
                visual={headingProp.visual}
              />
            )}
            {dismissButton}
          </Flex>
        )}

        <Flex container={{ direction: 'column' }} item={{ grow: 1 }} as={StyledDialogContent}>
          {!headingProp && dismissButton}
          {children && <StyledChildrenWrap>{children}</StyledChildrenWrap>}
          {progress}
        </Flex>
      </StyledInfoDialog>
    );
  }
);

export default InfoDialog;
